<template>
  <div class="app-container">
    <el-row :gutter="10">
      <el-col :offset="1" :xs="22" :sm="22" :md="22" :lg="22" :xl="22">
        <el-tabs type="border-card" @tab-click="handleClick">
          <el-tab-pane label="Configuración">
            <settings />
          </el-tab-pane>
          <el-tab-pane label="CSS">
            <css />
          </el-tab-pane>
          <el-tab-pane label="JavaScript">
            <javascript />
          </el-tab-pane>
          <!-- Custom CSS -->
          <el-row
            :gutter="10"
            v-loading="templateStore.loading"
            element-loading-text="Cargando..."
            type="flex"
            justify="center"
            v-if="isPage"
          >
            <el-col :xs="23" :sm="23" :md="23" :lg="23" :xl="23">
              <el-card class="box-card">
                <div slot="header" class="clearfix">
                  <span>Custom</span>
                </div>
                <div>
                  <codemirror
                    ref="editorJavascript"
                    v-model="content"
                    :options="cmOptions"
                  />
                </div>
                <div class="margin-10">
                  <el-button
                    @click.stop.prevent="saveCustom"
                    type="primary"
                    size="small"
                  >
                    Actualizar</el-button
                  >
                </div>
              </el-card>
            </el-col>
          </el-row>
          <!-- End Custom CSS -->
          <el-button
            @click="goToIndex"
            type="danger"
            size="small"
            class="go-index"
            icon="el-icon-back"
            v-if="isPage"
          >
            Cancelar</el-button
          >
        </el-tabs>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import { useStoreTemplate } from '../../../stores/template/templateStore'
import { useStoreCss } from '../../../stores/template/files/cssStore'
import { useStoreJavaScript } from '../../../stores/template/files/javascriptStore'
import { codemirror } from 'vue-codemirror'

import javascript from '../components/javascript.vue'
import css from '../components/css.vue'
import settings from '../components/settings.vue'

import _ from 'lodash'

const templateStore = useStoreTemplate()
const cssStore = useStoreCss()
const javascriptStore = useStoreJavaScript()

var tabSelected

export default {
  components: {
    javascript,
    css,
    settings,
    codemirror
  },
  data () {
    return {
      templateStore,
      cssStore,
      javascriptStore,
      form: {
        title: '',
        header: '',
        footer: ''
      },
      isPage: false,
      content: '',
      cmOptions: {
        tabSize: 4,
        mode: '',
        theme: 'dracula',
        lineNumbers: true,
        line: true
      }
    }
  },
  async created () {
    templateStore.isEdit = true
    templateStore.keyName = this.$route.params.keyName
    templateStore.typeTemplate = this.$route.params.typeTemplate

    const keyName = _.cloneDeep(templateStore.keyName)

    await cssStore.fetchCss(keyName)
    await cssStore.fetchCustomCss(keyName)

    await javascriptStore.fetchJavascript(keyName)
    await javascriptStore.fetchCustomJavascript(keyName)

    await templateStore.showTemplate(keyName)
  },
  methods: {
    handleClick (tab, event) {
      if (tab.$el.id === 'pane-0') {
        this.isPage = false
      } else if (tab.$el.id === 'pane-1') {
        this.isPage = true
        this.content = cssStore.content
        this.cmOptions.mode = 'text/css'
        tabSelected = tab.$el.id
      } else if (tab.$el.id === 'pane-2') {
        this.isPage = true
        this.content = javascriptStore.content
        this.cmOptions.mode = 'text/javascript'
        tabSelected = tab.$el.id
      }
    },
    saveCustom () {
      templateStore.loading = true
      const keyName = _.cloneDeep(templateStore.keyName)

      if (tabSelected === 'pane-1') {
        const customData = _.cloneDeep(this.content)
        cssStore
          .updateCustomCss(customData, keyName)
          .finally(() => (templateStore.loading = false))
      } else if (tabSelected === 'pane-2') {
        const customData = _.cloneDeep(this.content)
        javascriptStore
          .updateCustomJavascript(customData, keyName)
          .finally(() => (templateStore.loading = false))
      }
    },
    goToIndex () {
      this.$router.push({
        name: 'main.index'
      })
    }
  }
}
</script>

<style lang="scss" scoped>
#title {
  border: 1px solid #dcdcdc;
  padding: 15px 15px;
  border-radius: 3px;
  outline: none;
  box-shadow: inset 0px 1px 5px rgba(0, 0, 0, 0.1);
  font-family: inherit;
}
#slug,
#title {
  font-size: 14px;
}
#slug span {
  color: #06a4de;
}
.thanks {
  font-size: 12px;
}
.go-index {
  float: right;
}
</style>
